import React, { Component } from 'react';
import PageTemplate from './PageTemplate';
import TodoInput from './TodoInput';
import TodoList from './TodoList/TodoList';

export default class App extends Component {

  state = {
    id: 1,
    input: '',
    todoList: [
      { id: 0, text: ' ', done: true },
      { id: 1, text: '  ', done: false }
    ]
  }
  
  // TodoInput Component  
  _handleChange = (e) => {
      const { value } = e.target;
      this.setState({
        input: value
      })
  }

  _handleInsert = () => {
    const { todoList, input, id } = this.state;
    let increaseId = id + 1

    const newTodo = {
      id: increaseId,
      text: input,
      done: false
    }
    
    this.setState({
      id: increaseId,
      todoList: [...todoList, newTodo],
      input: ''
    })
  }

  // TodoList  TodoItem  
  _handleToggle = (id) => {
    const { todoList } = this.state;
    const itemIdx = todoList.findIndex(item => item.id === id);
    
    const toggledItem = {
      ...todoList[itemIdx],
      done: !todoList[itemIdx].done
    }

    this.setState({
      todoList: [
        ...todoList.slice(0, itemIdx),
        toggledItem,
        ...todoList.slice(itemIdx+1, todoList.length)
      ]
    })
  }

  _handleRemove = (id) => {
    const { todoList } = this.state;
    const itemIdx = todoList.findIndex(item => item.id === id);

    this.setState({
      todoList: [
        ...todoList.slice(0, itemIdx),
        ...todoList.slice(itemIdx+1, todoList.length)
      ]
    })
  }

  render() {
    // this(Component)    .      
    const { input, todoList } = this.state;
    const { _handleChange, _handleInsert, _handleToggle, _handleRemove } = this;
    return (
      <PageTemplate>
        <TodoInput onChange={_handleChange} onInsert={_handleInsert} value={input} />
        <TodoList items={todoList} onToggle={_handleToggle} onRemove={_handleRemove} />
      </PageTemplate>
    )
  }
}


